<template>
  <div class="hosts-manage">
    <div class="search-bar-1">
      <el-form :model="search" :inline="true" ref="ruleForm" :rules="search.rules">
        <el-form-item label="主机名称" prop="hostName">
          <el-input clearable v-model.trim="search.hostName" placeholder="请输入主机名称" @keyup.enter.native="onQuery"></el-input>
        </el-form-item>
        <el-form-item label="IP地址" prop="IP">
          <el-input clearable v-model.trim="search.IP" placeholder="请输入IP地址" @keyup.enter.native="onQuery"></el-input>
        </el-form-item>
        <el-form-item label="主机类型" prop="type">
          <el-select clearable v-model="search.type" placeholder="请选择主机类型">
            <el-option label="应用服务器" value="0"></el-option>
            <el-option label="数据库服务器" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="onQuery" type="primary" icon="el-icon-search">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="tool-bar">
      <el-link icon="el-icon-document-add" @click="onAdd">新增</el-link>
      <div class="split-line"></div>
      <el-link icon="el-icon-refresh" @click="fetchData">刷新</el-link>
    </div>
    <div class="gird-container">
      <el-table border stripe ref="table" :data="grid.rows" :height="grid.height" highlight-current-row v-loading="grid.loading" @selection-change="val => grid.selectedRow =val" :header-cell-style="{textAlign:'center'}">
        <!-- <el-table-column type="selection" align="center" width="50"></el-table-column> -->
        <el-table-column type="index" width="50" align="center" label="序号"></el-table-column>
        <el-table-column prop="hostName" label="被监控主机名称" align="left" show-overflow-tooltip></el-table-column>
        <el-table-column prop="IP" label="IP地址" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column prop="type" label="主机类型" align="left" show-overflow-tooltip :formatter="transType"></el-table-column>
        <el-table-column label="操作" align="center" width="100">
          <template slot-scope="scope">
            <el-tooltip effect="light" placement="bottom-start" content="查看" :enterable="false">
              <el-link style="font-size: 20px;margin: 0 5px 0 0;" icon="el-icon-document" @click="onView(scope.row)"></el-link>
            </el-tooltip>
            <el-tooltip effect="light" placement="bottom-start" content="编辑" :enterable="false">
              <el-link style="font-size: 20px;margin: 0 5px 0 0;" icon="el-icon-edit-outline" @click="onUpdate(scope.row)"></el-link>
            </el-tooltip>
            <el-tooltip effect="light" placement="bottom-start" content="删除" :enterable="false">
              <el-link style="font-size: 20px;margin: 0 5px 0 0;" icon="el-icon-delete" @click="onDelete(scope.row)"></el-link>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination background layout="total, sizes, prev, pager, next" :page-sizes="[10,30,50]" :page-size="pagging.pageSize" :current-page="pagging.pageNum" :total="pagging.total" @size-change="onChangePageSize" @current-change="onChangeCurrentPage"></el-pagination>
    </div>

    <el-dialog v-dialog width="700px" v-if="dialog.show" :visible.sync="dialog.show" :modal-append-to-body="false" :close-on-click-modal="false">
      <div slot="title" class="title">
        <i :class="dialogMode.icon"></i>
        <span>服务器主机信息管理 —— {{dialogMode.title}}</span>
      </div>
      <el-form ref="form" :rules="dialog.rules" :model="dialog.data" label-width="110px" :disabled="dialog.mode===3">
        <el-row>
          <el-col :span="12" v-if="dialog.mode===3">
            <el-form-item label="主机ID" prop="fid">
              <el-input v-model.trim="dialog.data.fid"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="主机名称" prop="hostName">
              <el-input v-model.trim="dialog.data.hostName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="IP地址" prop="IP">
              <el-input v-model.trim="dialog.data.IP"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="主机类型" prop="type">
              <el-select style="width: 100%;" v-model="dialog.data.type">
                <el-option label="应用服务器" value="0"></el-option>
                <el-option label="数据库服务器" value="1"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer">
        <el-button type="primary" @click="onSave" icon="fa fa-save" v-if="dialog.mode!==3">&nbsp;保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'HostsManager',
  data () {
    const validateIP = (isEmptyAllowed) => {
      return (rule, value, next) => {
        if (isEmptyAllowed) {
          if (value !== '' && !/^((25[0-5]|2[0-4]\d|[01]?\d\d?)\.){3}(25[0-5]|2[0-4]\d|[01]?\d\d?)$/.test(value)) {
            next('请输入正确的IP地址')
          } else {
            next()
          }
        } else {
          if (value === '' || !/^((25[0-5]|2[0-4]\d|[01]?\d\d?)\.){3}(25[0-5]|2[0-4]\d|[01]?\d\d?)$/.test(value)) {
            next('请输入正确的IP地址')
          } else {
            next()
          }
        }
      }
    }
    return {
      search: {
        hostName: '',
        IP: '',
        type: '',
        rules: {
          IP: [
            {
              validator: validateIP(true),
              trigger: 'blur'
            }
          ]
        }
      },
      grid: {
        rows: [],
        height: null,
        loading: false,
        selectedRow: []
      },
      pagging: {
        pageSize: 10,
        pageNum: 1,
        total: 0
      },
      dialog: {
        mode: null,
        show: false,
        data: null,
        rules: {
          IP: [
            {
              required: true,
              validator: validateIP(false),
              trigger: 'blur'
            }
          ],
          type: [
            {
              required: true,
              message: '请至少选择一个主机类型',
              trigger: 'change'
            }
          ],
          hostName: [
            {
              required: true,
              message: '请填写主机名称',
              trigger: 'blur'
            }
          ]
        }
      }
    }
  },
  computed: {
    dialogMode () {
      switch (this.dialog.mode) {
        case 1:
          return { title: '新增主机', icon: 'el-icon-document-add', message: '添加成功！' }
        case 2:
          return { title: '编辑主机', icon: 'el-icon-edit-outline', message: '修改成功！' }
        case 3:
          return { title: '查看', icon: 'el-icon-document' }
        default:
          return {}
      }
    }
  },
  mounted () {
    this.fetchData()
    this.resize()
    window.addEventListener('resize', this.resize)
  },
  beforeDestroy () {
    window.addEventListener('resize', this.resize)
  },
  methods: {
    async fetchData (params) {
      this.grid.loading = true
      let { data } = await this.$api.pagingData({
        params: {
          mapperId: 'com.bosssoft.monitor.dao.HostManageMapper.queryHosts',
          pageNum: this.pagging.pageNum,
          pageSize: this.pagging.pageSize,
          ...params
        }
      })
      this.grid.loading = false
      this.grid.rows = data.data.rows
      this.pagging.total = data.data.total
    },
    onChangeCurrentPage (pageNum) {
      this.pagging.pageNum = pageNum
      this.fetchData()
    },
    onChangePageSize (pageSize) {
      this.pagging.pageNum = 1
      this.pagging.pageSize = pageSize
      this.fetchData()
    },
    onAdd () {
      let model = {
        hostName: '',
        IP: '',
        type: ''
      }
      this.dialog.mode = 1
      this.dialog.show = true
      this.dialog.data = model
    },
    transType (row, column, cellValue, index) {
      return cellValue === '0' ? '应用服务器' : '数据库服务器'
    },
    validateForm (formName) {
      return (success, failed) => {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            success()
          } else {
            failed && failed()
          }
        })
      }
    },
    onQuery () {
      this.validateForm('ruleForm')(() => {
        this.pagging.pageNum = 1
        this.fetchData(this.search)
      })
    },
    resize () {
      this.grid.height = this.$el.clientHeight - 116
    },
    onUpdate (row) {
      this.dialog.mode = 2
      this.dialog.show = true
      this.dialog.data = Object.assign({}, row)
    },
    onView (row) {
      this.dialog.mode = 3
      this.dialog.show = true
      this.dialog.data = Object.assign({}, row)
    },
    onSave () {
      this.validateForm('form')(async () => {
        try {
          if (this.dialog.mode === 1) {
            await this.$api.add({
              data: {
                mapperId: 'com.bosssoft.monitor.dao.HostManageMapper.insertHost',
                ...this.dialog.data
              }
            })
          } else {
            const { fid, hostName, IP, type } = this.dialog.data
            await this.$api.modify({
              data: {
                mapperId: 'com.bosssoft.monitor.dao.HostManageMapper.updateHost',
                fid,
                hostName,
                IP,
                type
              }
            })
          }
          this.fetchData()
          this.dialog.show = false
          this.$message.success(this.dialogMode.message)
        } catch (error) {
          error.message && this.$message.error(error.message)
        }
      })
    },
    async onDelete (row) {
      try {
        this.$confirm('您确定要删除当前选中的记录吗?', '确认删除', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          await this.$api.remove({
            params: {
              mapperId: 'com.bosssoft.monitor.dao.HostManageMapper.deleteHost',
              fid: row.fid
            }
          })
          this.fetchData()
          this.$message.success('删除成功！')
        }).catch(() => { })
      } catch (error) {
        error.message && this.$message.error(error.message)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.hosts-manage {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .search-bar-1 {
    height: 60px;
  }
  .grid-container {
    flex: 1;
    overflow: auto;
  }
}
</style>
